.App {
	text-align: center;
	background-color: var(--bg);
	min-height: 100vh;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: var(--bg);
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.content {
	display: flex;
	align-items: center;
	justify-content: center;
}

.logo {
	cursor: pointer;
	justify-content: flex-start;
	font-weight: bolder;
	font-size: xx-large;
	margin-right: auto;
	color: var(--text-color);
	margin-left: 1rem;
}
